@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body,
html {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  touch-action: pan-y;
}

a {
  color: inherit;
  text-decoration: inherit;
}

::selection {
  background: transparent;
  color: #3a7d42;
}

.App {
  max-width: 100vw;
  overflow-x: hidden;
}
.page {
  overflow-x: hidden;
  padding: 0 5%;
  display: flex;
  min-height: 90vh;
  margin-top: 10vh;
}

.header {
  background: white;
  height: 10vh;
  position: fixed;
  top: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid lightgrey;
  z-index: 100;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.logo-section {
  padding: 0 2%;
  height: 60%;
}

.logo {
  height: 100%;
}

.navlinks-section {
  padding: 0 2%;
  height: 100%;
  display: flex;
  align-items: center;
}

.navlink {
  margin: 0 8px;
  font-size: 16px;
  transition-duration: 0.4s;
}

.navlink:hover {
  color: #85b14a;
}

.activenavlink {
  transition-duration: 0.4s;
  color: #3a7d42;
}

.activenavlink:hover {
  color: #3a7d42;
  text-decoration: none;
}

.footer {
  border-top: 1px solid lightgrey;
  background: white;
  bottom: 0;
  display: flex;
  flex-direction: column;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.footer-body {
  background: whitesmoke;
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
  font-size: 14px;
}

.footer-body-company-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.footer-body-company-section-logo-link {
  margin: 10px 0;
}

.footer-body-company-section-logo {
  height: 70px;
}

.footer-body-section {
  display: flex;
  flex-direction: column;
}

.footer-body-section-heading {
  font-size: 16px;
  font-weight: 500;
  margin: 9px 0;
}

.footer-body-section-navlink {
  margin: 3px 0;
}

.footer-body-contactus-item {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.footer-body-contactus-item-icon {
  margin: 0 10px 0 0;
  height: 18px;
}

.footer-body-contactus-item-typo {
  font-weight: 500;
  font-size: 13px;
}

.footer-copyright-section {
  padding: 10px 5%;
  background: white;
  border-top: 1px solid lightgrey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 10px;
  color: gray;
}

@media only screen and (max-width: 600px) {
  .footer-body {
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
  }

  .footer-body-section {
    margin: 20px 0;
  }

  .footer-body-contactus-item {
    flex-direction: column;
    margin: 10px;
    padding: 10px;
  }

  .footer-body-contactus-item-icon {
    height: 32px;
    margin-bottom: 10px;
  }

  .footer-body-company-section {
    margin: 10px 0;
  }

  .footer-copyright-section {
    flex-direction: column;
  }

  .footer-copyright {
    margin: 5px 0;
  }
}

.homepage {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.homepage-section {
  min-height: 90vh;
  width: 100%;
  margin: 10vh 0;
}

.section-short {
  min-height: 20vh;
  width: 100%;
  margin: 10vh 0;
}

.homepage-section-horizontal {
  min-height: 90vh;
  width: 100%;
  margin: 10vh 0;
  display: flex;
}

.RHS {
  width: 50%;
  margin: 0 50px;
}

.LHS {
  margin: 0 50px;
  width: 50%;
}

.hero-section {
  height: 80vh;
  width: 100vw;
  /* margin-top: -2vh; */
}

.slider {
  height: 100%;
  width: 100%;
  display: flex;
}

.each-slide {
  height: 70vh;
  display: flex;
  width: 100%;
}

.each-slide-image {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  color: white;
  transition-duration: 0.4s;
}

.each-slide-image-info {
  margin: 50px;
  background: black;
  min-height: 50px;
  padding: 10px 20px;
  min-width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.each-slide-image-info-title {
  font-weight: 500;
}

.each-slide-image-info-location {
  font-size: 12px;
}

.each-slide-image-info-learnmore {
  font-size: 10px;
  margin: 4px;
  width: 100%;
  text-align: right;
  fill: white;
}

.each-slide-image-info-learnmore-icon {
  height: 12px;
  width: 12px;
}

.error-500-page {
  /* background: lavender; */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
}

.error-500-page-errorcode-section {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.error-500-page-errorcode {
  font-size: 8rem;
  font-weight: 900;
}

.loading-page {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 90vh;
  align-items: center;
  touch-action: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-content: center;
}

h1 {
  margin: 1em 0;
  border-bottom: 1px dashed;
  padding-bottom: 1em;
  font-weight: lighter;
}

p {
  font-style: italic;
}

.loader {
  margin: 0 0 20px;
  width: 20%;
  text-align: center;
  display: inline-block;
  vertical-align: top;
}

.loading-message {
  /* font-style: italic; */
  opacity: .5;
/* font-family: 'Courier New', Courier, monospace; */
}

.homepage-news-section {
  width: 100%;
  min-height: 100%;
  cursor: default;
  border: 1px solid lightgray;
  padding: 20px;
}

.news-heading{
 font-weight: 700;
 margin-bottom: 10px;
 font-size: 12px; 
}

.homepage-news-list {
  border: 1px solid lightgray;
}

.news-item {
  padding: 8px 10px;
  border: 1px solid lightgray;
  display: flex;
  align-items: center;
}

.news-item-newscontent {
  font-size: 14px;
  text-align: justify;
  max-width: 100%;
  width: 95%;
}

.news-item-date {
  text-align: center;
  display: flex;
  border-radius: 5px;
  padding: 3px 2px;
  font-size: 10px;
  align-items: center;
  width: 5%;
  margin-right: 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.error-page {
  display: flex;
  width: 100vw;
  height: 90vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.error-code {
  height: 25vh;
}

.error-page-back-button {
  outline: none;
  font-weight: 600;
  margin: 35px 0;
  cursor: pointer;
  padding: 10px;
  transition-duration: 0.4s;
  border-radius: 4px;
  color: white;
  background: #3a7d42;
  border: 2px solid white;
}

.error-page-back-button:hover {
  border: 2px solid #3a7d42;
  color: #3a7d42;
  background: white;
}

.projects-page {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.projects-page-heading {
  font-weight: bold;
  font-size: 32px;
  margin: 20px 0;
  padding-bottom: 20px;
  border-bottom: 1px solid lightgrey;
}

.projects-page-projects-list {
  min-height: 100px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 50px;
  justify-content: space-evenly;
}

.projects-page-project-item {
  margin: 20px 10px;
  background: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  width: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition-duration: 0.4s;
  overflow: hidden;
}

.projects-page-project-item-image {
  width: 100%;
  height: 160px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.projects-page-project-item-name {
  margin: 20px 10px 0 10px;
  transition-duration: 0.4s;
  font-weight: 700;
  margin-top: 10px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

.projects-page-project-item-location {
  width: 100%;
  font-size: 12px;
  margin: 10px 0;
  color: grey;
}

.projects-page-project-item-learnmore {
  border-top: 1px solid black;
  font-size: 12px;
}

.projects-page-project-item:hover {
  border: 1px solid grey;
}

.projects-page-project-item:hover .projects-page-project-item-name {
  color: #3a7d42;
}

.project-info-page {
  display: flex;
  width: 100%;
  min-height: 90vh;
  padding: 0 0 10vh 0;
}

.project-info-LHS {
  width: 50%;
  height: 100%;
  margin: 0 50px;
}

.project-info-each-slide {
  height: 50vh;
  width: 100%;
  margin: 20vh 0;
}

.project-info-each-slide-image {
  display: flex;
  height: 100%;
  width: 50vw;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.project-info-RHS {
  display: flex;
  flex-direction: column;
  width: 50vw;
  justify-content: center;
}

.project-info-page-slider-arrow {
  fill: darkgrey;
  cursor: pointer;
  transition-duration: 0.4s;
  height: 28px;
}

.project-info-page-slider-arrow:hover {
  fill: black;
}

.slider-arrow-lett {
  padding-right: 12px;
}

.slider-arrow-right {
  padding-left: 12px;
}

.project-info-name {
  font-size: 42px;
  font-weight: 900;
  color: #3a7d42;
}

.project-info-location {
  color: grey;
  font-size: 14px;
  margin-bottom: 16px;
}

.project-info-type {
  color: #3a7d42;
  font-weight: bold;
  font-size: 18px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.project-info-architect {
  margin-bottom: 10px;
}

.project-info-client {
  margin-bottom: 10px;
}

.project-info-year {
  margin-bottom: 10px;
}

.project-info-description {
  text-align: justify;
  max-height: 45vh;
  overflow: scroll;
  padding: 10px 15px;
  border: 1px solid lightgray;
  background: whitesmoke;
}

